
.filter--cards--wrapers{ 
  display: block;
}

.setting--cards{  
  padding: 25px 4px;
  margin: 1px 9px;
  border-bottom: 1px solid #b6b6b6;
  max-height: 40px;
  display: flex;
}

.setting--cards:last-child {
  border-bottom: 1px solid transparent;
}



.setting--cards ._label_ {
  width: 75%;  
  opacity: 1;  
  transition: width ease .28s, opacity ease 1.28s;
  padding: 1px 6px;
}

.setting--cards ._label_  ._text_{
  font: 500 15px/19px "Roboto","Arial","Open Sans",sans-serif;
  color: #454545;
  text-align: left;
}

.setting--cards ._actions_ {
  width: 20%;  
  opacity: 1;  
  transition: width ease .28s, opacity ease 1.28s;
}

.setting--cards ._signal_ {
  width: 8%;  
  opacity: 1;  
  transition: width ease .28s, opacity ease 1.28s;
}



.setting--cards  .timer {
  width: 45%; 
  opacity: 1;  
  transition: width ease .28s, opacity ease 1.28s;
  padding: 1px 6px;
}

.setting--cards  .timer ._text_{
  font: 500 15px/19px "Roboto","Arial","Open Sans",sans-serif;
  color: #6c6c6c;  
  text-align: left;
}


.setting--cards .actionsTimer {
  width: 45%;
  transition: width ease .28s, opacity ease .3s;
}


.setting--cards .actionsTimer ._counter{
  font: 600 19px/19px "Roboto","Arial","Open Sans",sans-serif;
  color: #4c4c4c;
  text-align: right;
}






.filter--cards{  
  padding: 15px 4px;
  margin: 15px 10px;
  border-bottom: 1px solid #b2b2b2;
}




.filter--cards:last-child {
  border-bottom: 1px solid transparent;
}



.top--secction{
  text-align: left;
  display: flex;
}

.active--secction{
  text-align: left;
  display: flex;
  margin: 7px 0;
}

.filter--cards .top--secction .service--areas {
  width: 75%;
  max-height: 60px;
  min-height: 60px;
  opacity: 1;  
  transition: width ease .28s, opacity ease 1.28s;
}



.filter--cards .top--secction .actions {
  width: 20%;
  max-height: 60px;
  min-height: 60px;
  transition: width ease .28s, opacity ease .3s;
}

.filter--cards .top--secction .service--areas .label{
  font: 500 15px/19px "Roboto","Arial","Open Sans",sans-serif;
  color: #454545;
}

.filter--cards .top--secction .service--areas .label.date{  
  color:var(--color-base--hover);
}

.filter--cards .top--secction .service--areas .area{
  font: 500 13px/15px "Roboto","Arial","Open Sans",sans-serif;
  color: #6c6c6c;
}

.filter--cards .top--secction .service--areas .email{
  font: 400 13px/26px "Roboto","Arial","Open Sans",sans-serif;
  color: #6c6c6c;
}

.filter--cards .top--secction.editing .service--areas {
  width: 0%;
  opacity: 0; 
  transition: width ease .28s, opacity ease .08s;
}


.filter--cards .active--secction.editing{
  width: 0%;
  opacity: 0; 
  transition: width ease .28s, opacity ease .08s;
}



.filter--cards .top--secction .actions .editing {
  width: 98%;
}







.filter--cards .top--secction .actions .pay-show{
  transform: scale(1);
  opacity: 1; 
  max-height: 60px;
  transition: transform ease .28s, opacity ease .08s, max-height ease .08s;
  cursor: pointer;
  padding: 10px 0px;
  font: 600 17px/19px "Roboto","Arial","Open Sans",sans-serif;
  color:  var(--color-base--hover);
  text-align: right;
}

.filter--cards .top--secction.editing .actions .pay-show{
  transform: scale(0);
  opacity: 0; 
  max-height: 0px;
}


.filter--cards .top--secction .actions .edit-pay{
  transform: scale(0);
  opacity: 0; 
  max-height: 0px;
  transition: transform ease .28s, opacity ease .08s, max-height ease .08s;
}


.filter--cards .top--secction.editing .actions .editingpay .edit-pay{
  transform: scale(1);
  opacity: 1; 
  max-height: 60px;
  display: flex;
}


.filter--cards .top--secction.editing .actions .editingpayHour .edit-pay{
  transform: scale(1);
  opacity: 1; 
  max-height: 60px;
  display: flex;
}


.edit-pay .input--minimun--pay{
  display: -webkit-inline-flex;
  display: inline-flex;
  height: 36px;
  margin: 0 4px;
  min-width: 164px;
  max-width: 220px;
  padding: 0px 20px;
}


.edit-pay .btn--minimun--pay{
  display: inline-flex;
  height: 36px;
  margin: 4px;
  min-width: 64px;
  padding: 7px 23px;
}





